.App {
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100vw;
}

.header-container {
  background-color: #fff;
  box-shadow: 0px 5px 5px rgba(0, 0, 0, 0.1);
}

.header {
  display: flex;
  align-items: center;
  padding: 20px;
  width: 100%;
  height: 90px;
  font-family: Inter, sans-serif;
  background: linear-gradient(90deg, #672280 1.18%, #a626d3 100%);
  color: white;
}

.hd--logo {
  width: 50px;
  margin: auto 20px;
}

.hd--title {
  font-size: 1.5rem;
  font-weight: bold;
  margin: auto 5px;
  margin-right: auto;
}

.hd--subtitle {
  font-size: 1.2rem;
  font-weight: lighter;
  margin-right: 20px;
}

.main {
  padding: 36px;
}

.form {
  display: grid;
  grid-template: 50px 50px/ 1fr 1fr 75px;
  gap: 20px;
}

.form--inp {
  font-family: Inter, sans-serif;
  border-radius: 8px;
  border: 2px solid #d5d4d8;
  padding: 1rem;
  font-size: 1.2rem;
}

.form--inp:hover {
  box-shadow: 0px 0px 5px rgba(67, 22, 80, 0.5);
}

.form--inp:focus {
  outline: none;
  border: 1px solid #007bff;
}

.form--button {
  border-radius: 8px;
  font-family: Inter, sans-serif;
  grid-column: 1 / -1;
  font-size: 1.2rem;
  border: none;
  color: white;
  cursor: pointer;
  background: linear-gradient(90deg, #672280 1.18%, #a626d3 100%);
}

.form--button:hover {
  box-shadow: 0px 0px 5px rgba(67, 22, 80, 0.5);
  background: linear-gradient(90deg, #562170 1.18%, #9616e3 100%);
}

.number-input {
  text-indent: 10px;
  border-radius: 8px;
  border: 2px solid #d5d4d8;
  font-size: 1.2rem;
  font-family: monospace;
}

.img-result {
  color: #562170;
  max-width: 100%;
  max-height: 100%;
  object-fit: cover;
  border-radius: 2px;
  margin-top: 20px;
  border: 1px solid #007bff;
}

.meme {
  position: relative;
  text-align: center;
}

.meme-text {
  position: absolute;
  font-family: Anton, sans-serif;
  color: white;
  text-align: center;
  width: 100%;
  -webkit-text-stroke: 2px black;
  font-weight: 600;
}

.top {
  top: 0;
}

.bottom {
  bottom: 0;
}
